const CharacterHighlighter = ({ children }: { children: string }) => {
  return (
    <>
      {
        children.split('').map((char) => {
          const charCode = char.charCodeAt(0);
          if (charCode <= 32 || charCode >= 127) {
            return <span className="text-orange-400 underline">{char}</span>;
          }
          return char;
        })
        // .join('')
      }
    </>
  );
};

export default CharacterHighlighter;
